import axios from "axios";
axios.defaults.baseURL = "https://www.zzgoodqc.cn"; //全局配置ip地址 写个这别的就不要在写了

axios.defaults.timeout = 30000; //超过30s报错

//添加响应拦截器
axios.interceptors.response.use(
  function (response) {
    //可以写if判断，提前拦截错误信息
    return response;
  },
  function (err) {
    return Promise.reject(err);
  }
);

//post封装
export function apiPost(url, params) {
  return new Promise((resolve, reject) => {
    // console.log(params,'111')
    axios({
      method: "post",
      url: url,
      headers:{"strheader":'333333'},
      data: params,
    })
      .then((res) => {
        resolve(res.data);
      })
      .catch((err) => {
        reject(err.data);
      });
  });
}

//get 封装
export function apiGet(url, params) {
  return new Promise((resolve, reject) => {
    axios
      .get(url, {
        params: params,
        headers: { token: sessionStorage.getItem("token") },
      })
      .then((res) => {
        resolve(res.data);
      })
      .catch((err) => {
        reject(err.data);
      });
  });
}
