import { apiPost } from "./api";

export function getList(data) {
  return new Promise((resolve, reject) => {
    apiPost("/index.php/index/callcenter/getheaders",data).then((res) => {
        resolve(res);
      })
      .catch((err) => {
        reject(err);
      });
  });
}
export function getList1(data) {
    return new Promise((resolve, reject) => {
      apiPost("/index.php/index/index/mycenter",data).then((res) => {
          resolve(res);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }