<template>
  <div class="about">
    <h1>作业</h1>
    <input type="file" ref="fileInput" @change="handleFileChange" />
    <img :src="imageUrl" alt="" v-if="imageUrl" class="img">
    <br><br>
    <div class="box">
      <el-input v-model="names" class="el-input" placeholder="请输入内容"></el-input><br><br>
      <el-input v-model="userid" class="el-input" placeholder="请输入id"></el-input><br><br>
      <el-radio v-model="sex" label="0">男</el-radio>
      <el-radio v-model="sex" label="1">女</el-radio><br><br>
      <el-input v-model="iphone" class="el-input" placeholder="请输入电话"></el-input><br><br>
      <textarea name="" id="" v-model="mydes" cols="30" rows="10"></textarea><br><br>
      <el-button type="primary" @click="add()">立即创建</el-button>
      <el-button @click="qu()">取消创建</el-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import md5 from 'js-md5';
import { getList, getList1 } from '../api/http'
export default {
  name: '6AboutView',

  data() {
    return {
      imageUrl: '',
      name: "111",
      role: "222",
      str: "nbsp123ok",
      imageFile: null,
      names: '',
      userid: '',
      sex: 1,
      iphone: '',
      mydes: '',
      img: ''
    };
  },

  mounted() {
    this.api()
  },

  methods: {
    api() {
      let box = {
        name: this.name,
        role: this.role,
        sign: md5(this.name + this.role + this.str + '333333')
      }
      getList(box).then(res => {
        console.log(res)
      })

    },
    handleFileChange(event) {
      const file = event.target.files[0];
      const reader = new FileReader();
      reader.onload = (e) => {
        const base64Data = e.target.result;
        const formData = new FormData();
        formData.append('imgurl', base64Data);
        axios.post('https://www.zzgoodqc.cn/index.php/index/upload/uploadimg', formData)
          .then(res => {
            const imagePath = res.data.data.url;
            // 拼接接口域名或IP
            this.imageUrl = `http://47.94.4.201/${imagePath}`;
            console.log(res.data);
          })
          .catch(error => {
            console.error(error);
            // 根据需要处理错误
          });
      }
      reader.readAsDataURL(file);
    },
    add() {
      let arr = {
        name: this.names,
        userid: this.userid,
        sex: this.sex,
        iphone: this.iphone,
        mydes: this.mydes
      }
      getList1(arr).then(res => {
        console.log(res);
      })

    },
    qu() {
      this.names = this.userid = this.sex = this.iphone = this.mydes = ''
    }
  },
  computed: {
    fullImageUrl() {
      return this.imageUrl;
    }
  },
}
</script>
